  *{
    font-family: SharpSansSemiBold;
  }
  
  body{
    background-color: #FEE5C8;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: SharpSans;
    overflow-x: hidden;
  }
  .outline-div{
    border: 1px solid #FEBD72;
    width: 100%;
    height: 100vh;
    margin: 10px;
    border-radius: 30px;
  }
  
  .app-name-div{
    display: flex;
    justify-content: left;
  }
  
  .app-name{
    font-family: "RocGroteskBold";
    margin-left: 40px;
  }
  
  @media only screen and (max-width: 600px) {
    .app-name{
      font-size: 30px;
      
    }
  
    .app-name-div{
      justify-content: center;
    }
  }
  
  .main-card-container{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .main-card-div{
    border-radius: 30px;
    background-color: #FFD7A8;
    width: 810px;
    height: 460px;
    box-shadow: 0px 16px 58.20000076293945px -34px rgba(0, 0, 0, 0.69);
  }
  
  @media only screen and (max-width: 600px) {
    .main-card-div{
      margin-top: 20px;
      width: 340px;
      height: 500px;
    }
  
    .user-id-container{
      margin-top: 10px;
    }
  
    .your-unique-id{
      font-size: 12px;
    }
  
    .your-id-label{
      font-size: 12px;
    }
  }
  
  .user-id-container{
    display: flex;
    justify-content: center;
    margin: 40px 0 15px 0 ;
  }
  
  .your-id-label{
    display: inline-block;
    font-family: SharpSansBold;
    color: #333333;
    margin: 0 5px;
  }
  
  .your-unique-id{
    display: inline-block;
    font-family: SharpSansExtraBold;
    color: #000;
  }
  
  .copy-svg-div{
    display: inline-block;
    margin-left: 20px;
  }
  
  .copy-svg{
    width: 20px;
    cursor: pointer;
    vertical-align: middle;
    padding-bottom: 7px;
  }
  
  .input-bar-div{
    display: inline-block;
    margin-right: 15px;
  }
  
  .connect-button-div{
    display: inline-block;
    vertical-align: middle;
  }
  
  .input-section-div{
    margin-top: 30px;
  }
  
  .conn-label{
    font-family: SharpSansBold;
    color: #333333;
    margin-left: 5px;
  }
  
  .id-input-bar{
    font-family: SharpSansBold;
    padding: 10px 10px 10px 15px;
    vertical-align: middle;
    border: 1.7px solid #FEBD72;
    background-color: #FEE5C8;
    border-radius: 12px;
    width: 450px;
  }
  
  .id-input-bar:hover{
    cursor: text;
  }
  
  .id-input-bar:focus{
    outline: none;
  }
  
  .connect-button{
    padding: 7px 20px 7px 20px;
    border: 1px solid #684E30;
    border-radius: 10px;
    background-color: #FF9B28;
    font-family: SharpSansExtraBold;
    font-size: 18px;
  }
  
  .connect-button:hover{
    cursor: pointer;
  }
  
  .connect-button:focus{
    outline: none;
  }
  
  .conn-status{
    font-family: SharpSansSemiBold;
    font-size: 12px;
    margin-left: 5px;
    color: #333333;
  }
  
  .sprint-span{
    font-family: SharpSansSemiBoldItalic;;
  }
  
  .conn-status-div{
    margin-top: 15px;
  }
  
  .separation-line-div{
    display: flex;
    justify-content: center;
    margin: 20px 0px;
  
  }
  
  .separation-line{
    width: 500px;
    border: 0.7px rgba(180.62, 165.95, 149.02, 0.56) solid;
  }
  
  .files-container{
    display: flex;
    justify-content: center;
  
  }
  
  
  .copy-status-box {
    position: fixed;
    top: 25px;
    left: 42%;
    transform: translateX(-50%);
    background-color: #FFD7A8;
    padding: 10px;
    border-radius: 15px;
    border: 2px solid #FEBD72;
    font-size: 16px;
    opacity: 0;
    display: none;
    transition: opacity 0.5s ease-in-out;
  }
  
  .copy-status-box.show {
    display: block;
    opacity: 1;
    animation: slide-bottom 0.5s cubic-bezier(0.785, 0.135, 0.150, 0.860) both;
  }
  
  .copy-status-box.outro {
    animation: slide-top 0.5s cubic-bezier(0.860, 0.000, 0.070, 1.000) both;
  }
  
  @-webkit-keyframes slide-bottom {
    0% {
      -webkit-transform: translateY(-100px);
              transform: translateY(-100px);
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }
  
  @keyframes slide-bottom {
    0% {
      -webkit-transform: translateY(-100px);
              transform: translateY(-100px);
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }
  
  @-webkit-keyframes slide-top {
    0% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
    100% {
      -webkit-transform: translateY(-100px);
              transform: translateY(-100px);
    }
  }
  
  @keyframes slide-top {
    0% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
    100% {
      -webkit-transform: translateY(-100px);
              transform: translateY(-100px);
    }
  }
  
  .user-id-container.faded {
    opacity: 0.7;
    pointer-events: none;
  }
  
  
  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5); /* Adjust the alpha channel for the desired opacity */
    z-index: 999; /* Set a high z-index to make sure it overlays everything */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  
  .connection-approval-modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #c09666;
    width: 400px;
    height: 150px;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .approval-label {
    font-size: 18px;
    font-family: SharpSansExtraBold;
    margin-bottom: 10px;
  }
  
  .approval-label span{
    display: block;
    margin-bottom: 5px;
    font-size: 16px;
  }
  
  .approval-buttons {
    margin-top: 25px;
    display: flex;
    justify-content: center;
  }
  
  .accept-button{
    background-color: #3a230c;
    color: #ffffff;
    border: none;
    padding: 10px 20px;
    margin: 0 10px;
    font-size: 18px;
    font-family: SharpSansExtraBold;
    cursor: pointer;
    border-radius: 10px;
  }
  
  .reject-button {
    background-color: #fae6cf;
    color: #000;
    border: none;
    padding: 10px 20px;
    margin: 0 10px;
    font-size: 18px;
    font-family: SharpSansExtraBold;
    cursor: pointer;
    border-radius: 10px;
  }
  
  .input-bar{
    padding: 10px;
    border: 1.5px solid #FEBD72;
    border-radius: 12px;
    outline: none;
    background-color: #FEE5C8;
    outline-color: #FEBD72;
    width: 300px;
  }

  .send-button{
    position: absolute;
    right: -15px; 
    top: 7px;
    border: 0;
    background-color: #FEE5C8;
    outline: none;
    z-index: 2;
    cursor: pointer;
  }

  .text-input-div{
    position: relative;
    width: 300px;
  }

  .message-container{
    display: flex;
    justify-content: center;
  }